import Axios from "axios";
import { useSelector } from "react-redux";
import React, { useState, useEffect } from "react";
import { Form, Input, TextArea, Button, Select } from "semantic-ui-react";
import { getTokenConfig } from "../actions/auth";
import { useParams } from "react-router-dom";
import { updatecourse, postcourse } from "../actions/courseactions";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router";
import { clearmessage } from "../actions/messages";

export default function Post_new_course() {
  const history = useHistory();
  const message = useSelector((state) => state.Message);
  const dispatch = useDispatch();
  const { slug } = useParams();
  const [formData, setFormData] = useState({
    title: "",
    _from: "",
    desc: "",
    link: "",
  });

  useEffect(() => {
    if (slug) {
      Axios.get(`/api/v1/course/${slug}/?format=json`).then((res) =>
        setFormData(res.data)
      );
    }
  }, []);

  useEffect(() => {
    if (message.status === 400) {
      alert(JSON.stringify(message.msg));
    } else if (message.postcourse || message.updatecourse) {
      setFormData({
        title: "",
        _from: "",
        desc: "",
        link: "",
      });

      history.push("/");
    }
    dispatch(clearmessage());
  }, [message]);

  const handlesubmit = () => {
    const body = JSON.stringify(formData);
    if (slug) {
      dispatch(updatecourse(slug, body));
    } else {
      const a = dispatch(postcourse(body));
      console.log(a);
    }
  };

  const onchange = (e, { name, value }) => {
    setFormData((prevstate) => {
      return { ...prevstate, [name]: value };
    });
  };

  return (
    <div className="new-course-form">
      <Form>
        <Form.Group widths="equal">
          <Form.Field
            id="form-input-control-first-name"
            control={Input}
            label="Course name"
            placeholder="Course name"
            name="title"
            onChange={onchange}
            value={formData.title}
          />
          <Form.Field
            id="form-input-control-last-name"
            control={Input}
            label="From"
            placeholder="From"
            name="_from"
            onChange={onchange}
            value={formData._from}
          />
        </Form.Group>
        <Form.Field
          id="form-textarea-control-opinion"
          control={TextArea}
          label="Description"
          placeholder="Description"
          name="desc"
          onChange={onchange}
          value={formData.desc}
        />
        <Form.Field
          id="form-input-control-error-email"
          control={Input}
          label="Link"
          placeholder="https://"
          name="link"
          onChange={onchange}
          value={formData.link}
        />
        <Form.Field
          id="form-button-control-public"
          control={Button}
          content="Submit"
          onClick={handlesubmit}
        />
      </Form>
    </div>
  );
}
