import {
  GET_COURSES,
  POST_COURSE,
  DELETE_COURSE,
  UPDATE_COURSE,
} from "./types";
import { getTokenConfig } from "./auth";
import Axios from "axios";
import { createMessage, returnErrors } from "./messages";

export const getcourses = () => (dispatch) => {
  Axios.get("/api/v1/course/?format=json")
    .then((res) => {
      dispatch({
        type: GET_COURSES,
        payload: res.data,
      });
    })
    .catch((err) =>
      dispatch(returnErrors(err.response.data, err.response.status))
    );
};

export const postcourse = (course) => (dispatch, token) => {
  Axios.post("/api/v1/course/", course, getTokenConfig(token))
    .then((res) => {
      dispatch(createMessage({ postcourse: "course posted" }));
      dispatch({
        type: POST_COURSE,
        payload: res.data,
      });
    })
    .catch((err) =>
      dispatch(returnErrors(err.response.data, err.response.status))
    );
};

export const deletecourse = (slug) => (dispatch, token) => {
  Axios.delete(`/api/v1/course/${slug}`, getTokenConfig(token))
    .then((res) => {
      dispatch(createMessage({ deletecourse: "course deleted" }));
      dispatch({
        type: DELETE_COURSE,
        payload: res.data,
      });
    })
    .catch((err) =>
      dispatch(returnErrors(err.response.data, err.response.status))
    );
};

export const updatecourse = (slug, course) => (dispatch, token) => {
  Axios.patch(`/api/v1/course/${slug}/`, course, getTokenConfig(token))
    .then((res) => {
      dispatch(createMessage({ updatecourse: "course updated" }));
      dispatch({
        type: DELETE_COURSE,
        payload: res.data,
      });
      dispatch({
        type: POST_COURSE,
        payload: res.data,
      });
    })
    .catch((err) =>
      dispatch(returnErrors(err.response.data, err.response.status))
    );
};
