import React, { Fragment, useEffect } from "react";
import Navbar from "./comps/Navbar";
import Content from "./comps/content";
import Axios from "axios";
import "./App.css";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import Login_register from "./comps/login_register";
import { Setlogintotrue } from "./actions/auth";
import { useDispatch } from "react-redux";
import Postnewcourse from "./comps/post_new_course";
import Coursepage from "./comps/course_page";
import { getcourses } from "./actions/courseactions";
import NotFound from "./comps/notfound";

function App() {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getcourses());

    const token = localStorage.getItem("token");
    const config = { headers: { Authorization: `Token ${token}` } };
    if (token) {
      Axios.get("/api/auth/user", config)
        .then((res) => {
          dispatch(
            Setlogintotrue({ token: token, username: res.data.username })
          );
        })
        .catch((e) => console.log(e));
    }
  }, []);
  return (
    <div className="App">
      <Router>
        <Navbar />
        <Fragment>
          <Switch>
            <Route exact path="/" component={Content} />
            <Route exact path="/login" component={Login_register} />
            <Route exact path="/post-course" component={Postnewcourse} />
            <Route exact path="/edit/:slug" component={Postnewcourse} />
            <Route exact path="/course/:slug" component={Coursepage} />
            <Route component={NotFound} />
          </Switch>
        </Fragment>
      </Router>
    </div>
  );
}

export default App;
