import {
  GET_COMMENTS,
  POST_COMMENT,
  DELETE_COMMENT,
  UPDATE_COMMENT,
  CLEAR_COMMENTS,
} from "../actions/types";

const initialstate = { comments: [] };

export default function (state = initialstate, action) {
  switch (action.type) {
    case GET_COMMENTS:
      return {
        ...state,
        comments: action.payload,
        loaded: true,
      };
    case DELETE_COMMENT:
      return {
        ...state,
        comments: state.comments.filter(
          (comment) => comment.id !== action.payload.id
        ),
      };
    case POST_COMMENT:
      return {
        ...state,
        comments: [...state.comments, action.payload],
      };
    case UPDATE_COMMENT:
      return {
        ...state,
        comments: [],
      };
    case CLEAR_COMMENTS:
      return { comments: [] };
    default:
      return state;
  }
}
