import {
  GET_COMMENTS,
  POST_COMMENT,
  DELETE_COMMENT,
  UPDATE_COMMENT,
  CLEAR_COMMENTS,
} from "./types";
import { getTokenConfig } from "./auth";
import Axios from "axios";
import { createMessage, returnErrors } from "./messages";

export const getcomments = (slug) => (dispatch) => {
  Axios.get(`/api/v1/comment/?course=${slug}&format=json`)
    .then((res) => {
      dispatch({
        type: GET_COMMENTS,
        payload: res.data,
      });
    })
    .catch((err) =>
      dispatch(returnErrors(err.response.data, err.response.status))
    );
};

export const postcomment = (comment) => (dispatch, getState) => {
  Axios.post("/api/v1/comment/", comment, getTokenConfig(getState))
    .then((res) => {
      dispatch(createMessage({ postcomment: "comment posted" }));
      dispatch({
        type: POST_COMMENT,
        payload: res.data,
      });
    })
    .catch((err) =>
      dispatch(returnErrors(err.response.data, err.response.status))
    );
};

export const deletecomment = (id) => (dispatch, token) => {
  Axios.delete(`/api/v1/comment/${id}/`, getTokenConfig(token))
    .then((res) => {
      dispatch(createMessage({ deletecomment: "comment deleted" }));
      console.log(res.data);
      dispatch({
        type: DELETE_COMMENT,
        payload: res.data,
      });
    })
    .catch((err) =>
      dispatch(returnErrors(err.response.data, err.response.status))
    );
};

export const updatecomment = (slug, comment) => (dispatch, token) => {
  Axios.post(`/api/v1/comment/${slug}`, comment, getTokenConfig(token))
    .then((res) => {
      dispatch(createMessage({ updatecomment: "comment updated" }));
      dispatch({
        type: UPDATE_COMMENT,
        payload: res.data,
      });
    })
    .catch((err) =>
      dispatch(returnErrors(err.response.data, err.response.status))
    );
};

export const clearcomments = () => (dispatch) => {
  dispatch({
    type: CLEAR_COMMENTS,
  });
};
