import Axios from "axios";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Button, Checkbox, Form, Comment, Header } from "semantic-ui-react";
import {
  postcomment,
  getcomments,
  deletecomment,
  clearcomments,
} from "../actions/comments";

import { clearmessage } from "../actions/messages";
import { deletecourse } from "../actions/courseactions";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";

export default function Course_page() {
  const history = useHistory();
  const comments = useSelector((state) => state.Comment.comments);
  const commentsloaded = useSelector((state) => state.Comment.loaded);
  const username = useSelector((state) => state.loginstate.username);
  const courses = useSelector((state) => state.Course.courses);
  const message = useSelector((state) => state.Message);
  const dispatch = useDispatch();
  const [formData, setFormData] = useState({ review: "" });
  const [Loading, setLoading] = useState(true);
  const { slug } = useParams();
  const [course, setCourse] = useState({
    title: "",
    _from: "",
    desc: "",
    id: "",
    username: "",
    link: "",
  });

  useEffect(() => {
    Axios.get(`/api/v1/course/${slug}/?format=json`).then((res) =>
      setCourse(res.data)
    );
    if (courses.length === 0) {
      Axios.get(`/api/v1/course/${slug}/?format=json`).then((res) =>
        setCourse(res.data)
      );
    } else {
      setCourse(courses.find((c) => c.slug === slug));
    }

    dispatch(getcomments(slug));

    return () => {
      dispatch(clearcomments());
    };
  }, []);

  useEffect(() => {
    if (message.status === 400) {
      alert(JSON.stringify(message.msg));
    } else if (message.deletecourse) {
      history.push("/");
    } else {
      setFormData({ review: "" });
    }
    dispatch(clearmessage());
  }, [message]);

  useEffect(() => {
    if (!commentsloaded) {
      console.log(commentsloaded);
      setLoading(true);
    } else setLoading(false);
    return () => {
      setLoading(true);
    };
  }, [comments]);

  const handelsubmit = () => {
    dispatch(postcomment({ comment: formData.review, course: course.slug }));
  };

  const onchange = (e, { name, value }) => {
    setFormData((prevstate) => {
      return { ...prevstate, [name]: value };
    });
  };

  return (
    <div className="course-page">
      <h2>{course.title}</h2>
      <p style={{ color: "gray" }}>By: {course._from}</p>
      <p>{course.desc}</p>

      <Button onClick={() => window.open(course.link, "_blank")} size="mini">
        Visit
      </Button>
      {username === course.username && (
        <Button onClick={(e) => history.push(`/edit/${slug}`)} size="mini">
          Edit
        </Button>
      )}
      {username === course.username && (
        <Button onClick={(e) => dispatch(deletecourse(slug))} size="mini">
          Delete
        </Button>
      )}
      <Comment.Group>
        <Header as="h3">Comments</Header>
        <Form>
          <Form.TextArea
            value={formData.review}
            name="review"
            onChange={onchange}
          />

          <Form.Button primary onClick={handelsubmit}>
            Submit
          </Form.Button>
        </Form>
        {Loading && <p>Loading comments...</p>}
        {comments.map((c) => (
          <Comment key={c.id}>
            <Comment.Content className="comment">
              <Comment.Author as="a">{c.username}</Comment.Author>
              <Comment.Metadata>
                <div>{new Date(c.datetime).toDateString()}</div>
              </Comment.Metadata>
              <Comment.Text>{c.comment}</Comment.Text>
              <Comment.Actions>
                {/* <Comment.Action>Reply</Comment.Action> */}
                {username === c.username && (
                  <Comment.Action
                    onClick={(e) => {
                      dispatch(deletecomment(c.id));
                    }}
                  >
                    Delete
                  </Comment.Action>
                )}
              </Comment.Actions>
            </Comment.Content>
          </Comment>
        ))}
      </Comment.Group>
    </div>
  );
}
